<template>
<div>
    <div>
    <v-toolbar
          dark
          color="primary"
    >
        <v-row>
            <v-btn @click="toAct">活动总页面</v-btn>
            <v-col></v-col>
            <v-btn @click="toTrace">资金流向总页面</v-btn>
            <v-col cols="6"></v-col>
            <v-toolbar-title>你好，{{username}}</v-toolbar-title>
            <v-col></v-col>
            <v-toolbar-title>积分：{{credit}}</v-toolbar-title>
        </v-row>
    </v-toolbar>
    </div>

    <div>
    <v-row>
        <v-col cols="5">
            <v-timeline align-top>
                <div v-for="(item, i) in deals" :key="i">
                    <v-timeline-item
                        color="blue"
                        small
                    >
                        <p>活动id：{{item.aid}}</p>
                        <p>区块哈希：{{item.hash}}</p>
                        <p>动物id：{{item.id}}</p>
                        <p>捐款金额：{{item.money}}</p>
                        <p>交易：{{item.tm}}</p>
                    </v-timeline-item>
                </div>
            </v-timeline>
        </v-col>
    </v-row>
    </div>
</div>
</template>

<script>
export default{
    name: "UserPage",
    data(){
        return {
            username:'',
            userInfo: {
                userName: "xxx",
                userId: "errewwew",
                credit: 10,
            },

            deals: []
        }
    },
    methods: {
                 toAct() {
                 this.$router.push("/activities");
                 },
                 toTrace() {
                 this.$router.push("/trace");
                 }
    },
    created(){
        this.username = localStorage.getItem("username");
        const _this = this;
        this.$axios.defaults.headers.common['token'] = localStorage.getItem("token");
        this.userInfo.userId = localStorage.getItem("id");
        this.$axios.get('/transaction/getInfo', {param:{'uid':this.userInfo.userId}}).then(res => {
            res.data.data.forEach(function(item){
                _this.deals.push(item)
            })
        } );

    }
}
</script>
