<template>
  <div id="app">
    <div class="pa-0" style="max-height:100vh;overflow:hidden;">
      <v-img src="https://cdn.vuetifyjs.com/images/cards/sunshine.jpg"
             height="100px" width="100vw"
      ></v-img>
      <v-card class="mx-auto elevation-5 text-center pa-5" max-width="350" style="border-radius:20px;position:relative;top:-50px;"
      >
        <v-card-subtitle class="title black--text pa-1">
          欢迎
        </v-card-subtitle>
        <v-card-subtitle class="pa-1">
          一切开始的地方 <br/> 让信任生长
        </v-card-subtitle>
        <v-text-field hide-details color="red" label="用户名" v-model="userinfo.loginUsername"></v-text-field>
        <v-text-field color="red" label="密码" type="password" v-model="userinfo.loginPassword"></v-text-field>
        <v-card-actions>
          <v-btn color="red" dark block rounded @click="userLogin()">
            注册
          </v-btn>
        </v-card-actions>
      </v-card>
      <div class="text-center" style="position:relative;top:-40px">
        <p class="ma-0">- 其他登陆方式 -</p>
        <v-btn color="grey" rounded outlined class="ma-2" width="200"><v-icon left color="blue">mdi-facebook</v-icon>用支付宝登录</v-btn><br/>
        <v-btn color="grey" rounded outlined class="ma-2" width="200"><v-icon left color="red">mdi-google</v-icon>用钉钉登录</v-btn>
<!--        <p class="mt-3">还没有账号? <span class="red&#45;&#45;text" style="cursor:pointer">注册一个</span></p>-->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:"registration",
  data(){
    return{
      userinfo:{
        loginUsername: '',
        loginPassword: ''
      }
    }
  },
  methods:{
    userLogin() {
      const _this = this;
      this.$axios.post('/login/user/registry', this.userinfo).then(res => {
        if(res.data){
          alert("注册成功");
          _this.$router.push('/login')
        }
      });
      // const _this = this;
      // this.$axios.post('login/user/registry', this.userinfo).then(res =>{
      //   _this.$store.commit("SET_TOKEN", res.data.data.token);
      //   localStorage.setItem('id', res.data.data.id);
      //   localStorage.setItem('username',this.userinfo.loginUsername);
      //   alert("注册成功");
      //   _this.$router.push("/user")
      // })
    },
  }

}
</script>