import Vue from 'vue'
import VueRouter from 'vue-router'
import Trace from '../views/Trace.vue'
import User from '../views/User.vue'
import Activities from '../views/Activities.vue'
import Homepage from '../views/Home.vue'
import About from '../views/About.vue'
import Login from '../views/Login.vue'
import Orglogin from '../views/OrgLogin'
//import axios from '../plugins/axios'
import Organization from '../views/Organization.vue'
import registration from "../views/registration";
import Animal from "../views/Animal";
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Homepage',
    component: Homepage
  },
 {
    path: '/organization',
    name: 'Organization',
    component: Organization
    },
    {
        path: '/registration',
        name: 'registration',
        component: registration
    },
    {
        path: '/orglogin',
        name: 'Orglogin',
        component: Orglogin
    },
   {
        path: '/login',
        name: 'Login',
        component: Login
    },
    {
        path: '/about',
        name: 'About',
        component: About
    },
    {
        path: '/trace',
        name: 'Trace',
        component: Trace
    },
    {
        path: '/user',
        name: 'User',
        component: User
    },
    {
        path: '/activities',
        name: 'Activities',
        component: Activities
    },
    {
        path: '/animal',
        name: 'animal',
        component: Animal
    }
]

const router = new VueRouter({
    mode: 'history',
    routes
})


router.beforeEach((to, from, next) => {
    let token = localStorage.getItem('token');
    if (to.path == '/' || to.path == '/login' || to.path == '/orglogin' || to.path == '/registration') {
        localStorage.clear();
        next();
    } else {
        if (token == '' || token == null) {
            next('/');
        } else {
            next();
        }
    }
})


export default router
