<template xmlns:v-solt="http://www.w3.org/1999/html">
  <div class="organization">
    <!--  <v-row class="head" justify="center" align = "center">-->
    <!--    <v-spacer cols = 8></v-spacer>-->
    <!--    <v-col cols=3 class="head-value">-->
    <!--      <div class="head-text">{{headtext}}</div>-->
    <!--    </v-col>-->
    <!--    <v-col cols=1 class="head-value">-->
    <!--      <div class="head-value">{{headvalue}}</div>-->
    <!--    </v-col>-->
    <!--  </v-row>-->

    <v-row rows="3" class="tab" align="center" justify="center">
      <!--    <v-col cols=8 class="tab-title" align="center">放心公益</v-col>-->
      <!--    <v-img src="https://z3.ax1x.com/2021/07/17/WQ2TA0.jpg" :contain="false"></v-img>-->
      <v-card class="barCard">
        <v-row align="start" justify="start"
          ><div class="barTxt">当前募集:{{ msg }}</div></v-row
        >
      </v-card>
    </v-row>

    <Orgact class="active" />

    <v-row class="work">
      <v-col>
        <v-card class="mx-auto" max-width="480" height="440">
          <v-img
            src="https://z3.ax1x.com/2021/07/17/WQaalV.png"
            :contain="false"
          ></v-img>
          <v-divider class="mt-6 mx-4" height="100"></v-divider>
          <v-row align="center" justify="center">
            <v-chip class="mr-2 workTxt workLoc1" @click="routeTotrace">
              查询链上资金流向
            </v-chip>

            <v-chip
              class="mr-2 workTxt workLoc2"
              @click="overlayVerify = !overlayVerify"
            >
              上传资金使用凭证
            </v-chip>
            <!--上传凭证事件绑定-->
            <v-overlay :absolute="absoluteVerify" :value="overlayVerify">
              <v-row>
                <v-text-field
                  label="活动编号"
                  :rules="rules"
                  hide-details="auto"
                  v-model="aid"
                ></v-text-field>
                <v-text-field
                  label="使用编号"
                  :rules="rules"
                  hide-details="auto"
                  v-model="id"
                ></v-text-field>
              </v-row>
              <!--              <v-row v-on:listenUpload="receiveUpload">-->
              <!--                <v-text-field label="上传图片" :rules="rules" hide-details="auto"></v-text-field>-->
              <!--                <uploadimage/>-->
              <template>
                <v-file-input
                  truncate-length="15"
                  v-model="file"
                ></v-file-input>
                <v-btn
                  :loading="loading3"
                  :disabled="loading3"
                  color="blue-grey"
                  class="ma-2 white--text"
                  @click="submit"
                >
                  Upload
                  <v-icon right dark> mdi-cloud-upload </v-icon>
                </v-btn>
              </template>
              <!--              </v-row>-->
              <v-divider></v-divider>
              <v-row>
                <v-col justify="center">
                  <v-btn color="success" @click="uploadfile">确定</v-btn>
                </v-col>
                <v-col justify="center">
                  <v-btn color="error" @click="overlayVerify = false"
                    >关闭</v-btn
                  >
                </v-col>
              </v-row>
            </v-overlay>
          </v-row>
        </v-card>
      </v-col>
      <v-col>
        <v-card class="mx-auto" max-width="480" height="440">
          <v-img
            src="https://z3.ax1x.com/2021/07/17/WQa6YR.png"
            :contain="false"
          ></v-img>
          <v-divider class="mt-6 mx-4" height="100"></v-divider>
          <v-row align="end" justify="center">
            <v-hover v-model="hover1">
              <v-btn
                class="mx-2 animalLoc1"
                fab
                dark
                color="indigo"
                @click="overlayAnimal = !overlayAnimal"
              >
                <v-icon dark> mdi-plus </v-icon>
              </v-btn>
            </v-hover>
            <!--添加动物事件绑定-->
            <v-badge
              :value="hover1"
              color="deep-orange"
              content="加入新朋友"
              left
              transition="slide-x-transition"
            ></v-badge>
            <v-overlay :absolute="absoluteAnimal" :value="overlayAnimal">
              <v-row>
                <v-text-field
                  label="动物名称"
                  :rules="rules"
                  hide-details="auto"
                  v-model="animalinfo.name"
                  autocomplete="none"
                ></v-text-field>
              </v-row>
              <v-row>
                <v-text-field
                  label="动物品种"
                  :rules="rules"
                  hide-details="auto"
                  v-model="animalinfo.type"
                ></v-text-field>
              </v-row>
              <v-row>
                <v-textarea
                  label="活动范围"
                  :rules="rules"
                  hide-details="auto"
                  v-model="animalinfo.actscope"
                ></v-textarea>
              </v-row>
              <v-row>
                <v-col justify="center">
                  <v-btn color="success" @click="addAnimalsuc">确定</v-btn>
                </v-col>
                <v-col justify="center">
                  <v-btn color="error" @click="overlayAnimal = false"
                    >关闭</v-btn
                  >
                </v-col>
              </v-row>
            </v-overlay>

            <v-hover v-model="hover2">
              <v-btn
                class="mx-2 animalLoc2"
                fab
                dark
                color="teal"
                @click="routeToanimal"
              >
                <v-icon dark> mdi-format-list-bulleted-square </v-icon>
              </v-btn>
            </v-hover>
            <v-badge
              :value="hover2"
              color="deep-orange"
              content="动物花名册"
              left
              transition="slide-x-transition"
            ></v-badge>
          </v-row>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import Orgact from "../components/Orgact";
//import Uploadimage from "../components/UploadImage";

export default {
  name: "Organization",
  components: { /*Uploadimage, */ Orgact },
  comments: {
    Orgact,
  },
  methods: {
    routeTotrace() {
      this.$router.push("/trace");
    },
    routeToanimal() {
      this.$router.push("/animal");
    },
    addAnimalsuc() {
      this.overlayAnimal = false;
      this.$axios.post("/animals/save", this.animalinfo).then(function () {
        alert("添加成功！");
      });
      (this.aniName = ""), (this.aniType = ""), (this.aniactScope = "");
    },
    receiveUpload: function (data) {
      this.uploadImage = new FormData();
      this.uploadImage = data;
    },
    changefile(event) {
      let file = event.target.files[0];
      console.log(file);
      let suffixName = file.name.substring(file.name.lastIndexOf(".") + 1);
      if (suffixName !== "jpg" && suffixName !== "png") {
        this.$message.warning("只能图片");
        return;
      }
      let formData = new FormData();
      formData.append("file", file);
      console.log(formData.get("file"));
      this.uploadbody.file = formData;
      this.$axios.post("flow/upload", this.uploadbody);
    },
    imageLoaded(event) {
      this.image = event.target.result;
    },
    submit() {
      let form = new FormData();
      form.append("file", this.file);
      form.append("aid", this.aid);
      form.append("id", this.id);
      this.$axios.post("flow/upload", form).then((res) => {
        if (res.data) {
          alert("添加成功");
        }
      });
    },
    addVerifysuc() {
      this.overlayVerify = false;
      alert(this.uploadImage);
      this.$axios
        .post(
          "/flow/upload",
          {
            params: {
              aid: this.verifyId,
              file: this.uploadImage,
            },
          },
          { headers: { "Content-Type": "multipart/form-data" } }
        )
        .then(function () {
          alert("添加成功！");
        });
    },
  },
  created() {
    const _this = this;
    this.$axios.get("/activity/getTotalMoney").then((res) => {
      _this.msg = res.data.data;
    });
  },
  data: function () {
    return {
      file: null,
      msg: "",
      imagefile: "",
      id: "",
      aid: "",
      animalinfo: {
        name: "",
        type: "",
        actscope: "",
      },
      hover1: false,
      hover2: false,
      overlayVerify: false,
      absoluteVerify: true,
      overlayAnimal: false,
      absoluteAnimal: true,
      verifyId: "",
      uploadImage: "",
    };
  },
};
</script>

<style scoped>
.barTxt {
  margin-left: 30px;
  margin-top: 30px;
  font-size: 80px;
  color: white;
  font-family: ysTitle;
}
.barCard {
  width: 90%;
  height: 300px;
  color: #385f73;
  background-image: url(https://z3.ax1x.com/2021/07/18/W8ku4J.jpg);
  background-image-width: 100%;
}
.organization {
  width: 100%;
  height: 1500px;
  background-color: #ffffff;
}

.ma-2 {
  font-size: 24px;
  color: #207de1;
  width: 80%;
}
.head {
  height: 80px;
  width: 100%;
  border-bottom: 1px solid #979797;
}
.head-text {
  /*font-family: SourceHanSansSC;*/
  color: #000000;
  font-size: 24px;
  text-align: right;
}
.head-value {
  /*font-family: YSBiaoTiHei;*/
  text-align: left;
  color: #207de1;
  font-size: 30px;
}

.func {
  margin-top: 64px;
}
.active {
  display: block;
  margin-top: 100px;
}
.tab-title {
  color: #000000;
  /*font-family: ZKQingKeHuangYou;*/
  font-size: 180px;
}

.work {
  margin-top: 80px;
}
.workTxt {
  /*height: 20px;*/
  display: block;
  float: left;
  align-left: center;
}
.workLoc1 {
  margin-right: 24px;
}
.workLoc2 {
  margin-left: 24px;
}
.animalLoc1 {
  display: block;
  margin-right: 80px;
  margin-top: 40px;
}
.animalLoc2 {
  display: block;
  margin-left: 400px;
  margin-top: 40px;
}
@font-face {
  font-family: ysTitle;
  src: url("../assets/Fontstyle.ttf");
}
</style>