<template>
  <div>
    <v-row>
      <v-container fluid>
        <v-data-iterator
          :items="items"
          :items-per-page.sync="itemsPerPage"
          :page.sync="page"
          :search="search"
          :sort-by="sortBy.toLowerCase()"
          :sort-desc="sortDesc"
          hide-default-footer
        >
          <template v-slot:header>
            <v-toolbar dark color="deep-purple darken-1" class="mb-1">
              <v-text-field
                v-model="search"
                clearable
                flat
                solo-inverted
                hide-details
                prepend-inner-icon="mdi-magnify"
                label="Search"
              ></v-text-field>
              <template v-if="$vuetify.breakpoint.mdAndUp">
                <v-spacer></v-spacer>
                <v-select
                  v-model="sortBy"
                  flat
                  solo-inverted
                  hide-details
                  :items="keys"
                  prepend-inner-icon="mdi-magnify"
                  label="Sort by"
                ></v-select>
                <v-spacer></v-spacer>
                <v-btn-toggle v-model="sortDesc" mandatory>
                  <v-btn large depressed color="yellow" :value="false">
                    <v-icon>mdi-arrow-up</v-icon>
                  </v-btn>
                  <v-btn large depressed color="yellow" :value="true">
                    <v-icon>mdi-arrow-down</v-icon>
                  </v-btn>
                </v-btn-toggle>
              </template>
            </v-toolbar>
          </template>

          <template v-slot:default="props">
            <v-row>
              <v-col
                v-for="item in props.items"
                :key="item.topic"
                cols="12"
                sm="6"
                md="4"
                lg="3"
              >
                <v-card>
                  <v-card-title class="subheading font-weight-bold"
                    >{{ item.id }}
                    {{ item.topic }}
                  </v-card-title>

                  <v-divider></v-divider>

                  <v-list dense>
                    <v-list-item
                      v-for="(key, index) in filteredKeys"
                      :key="index"
                    >
                      <v-list-item-content
                        :class="{ 'blue--text': sortBy === key }"
                      >
                        {{ key }}:
                      </v-list-item-content>
                      <v-list-item-content
                        class="align-end"
                        :class="{ 'blue--text': sortBy === key }"
                      >
                        {{ item[key.toLowerCase()] }}
                      </v-list-item-content>
                    </v-list-item>
                  </v-list>
                  <div class="text-center">
                    <v-progress-circular
                      :rotate="360"
                      :size="100"
                      :width="15"
                      :value="item.rate * 100"
                      color="teal"
                    >
                      {{ item.rate * 100 }}%
                    </v-progress-circular>
                  </div>
                  <v-img :src="item.url"></v-img>
                  <v-dialog v-model="dialog" persistent max-width="600px">
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn tile color="error" large v-bind="attrs" v-on="on">
                        <v-icon left> mdi-heart </v-icon>
                        献爱心
                      </v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        <span class="text-h5">输入捐助钱数</span>
                      </v-card-title>
                      <v-card-text>
                        <v-text-field
                          label="谢谢你'_'"
                          required
                          v-model="money"
                        >
                        </v-text-field>
                      </v-card-text>
                      <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn
                          color="blue darken-1"
                          text
                          @click="dialog = false"
                        >
                          关闭
                        </v-btn>
                        <v-btn
                          color="blue darken-1"
                          text
                          @click="givemoney(item.id)"
                        >
                          捐赠
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </v-card>
              </v-col>
            </v-row>
          </template>

          <sweet-modal ref="modal">
            <v-text-field
              v-model="money"
              label="money amount"
              required
            ></v-text-field>
            <v-button slot="button" v-on:click="givemoney" color="success"
              >确定</v-button
            >
          </sweet-modal>

          <template v-slot:footer>
            <v-row class="mt-2" align="center" justify="center">
              <span class="grey--text">Items per page</span>
              <v-menu offset-y>
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    dark
                    text
                    color="primary"
                    class="ml-2"
                    v-bind="attrs"
                    v-on="on"
                  >
                    {{ itemsPerPage }}
                    <v-icon>mdi-chevron-down</v-icon>
                  </v-btn>
                </template>
                <v-list>
                  <v-list-item
                    v-for="(number, index) in itemsPerPageArray"
                    :key="index"
                    @click="updateItemsPerPage(number)"
                  >
                    <v-list-item-title>{{ number }}</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>

              <v-spacer></v-spacer>

              <span class="mr-4 grey--text">
                Page {{ page }} of {{ numberOfPages }}
              </span>
              <v-btn
                fab
                dark
                color="deep-purple darken-1"
                class="mr-1"
                @click="formerPage"
              >
                <v-icon>mdi-chevron-left</v-icon>
              </v-btn>
              <v-btn
                fab
                dark
                color="deep-purple darken-1"
                class="ml-1"
                @click="nextPage"
              >
                <v-icon>mdi-chevron-right</v-icon>
              </v-btn>
            </v-row>
          </template>
        </v-data-iterator>
        <v-spacer></v-spacer>
      </v-container>
    </v-row>

    <v-row>
      <v-container>
        <v-row align="center" justify="center">
          <v-btn color="secondary" elevation="9" large @click="toTrace">
            查看资金流向
          </v-btn>
        </v-row>
      </v-container>
    </v-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      money: "",
      userinfo: {
        userpoint: "",
        useraccount: "",
      },
      itemsPerPageArray: [4, 8, 12],
      search: "",
      filter: {},
      sortDesc: false,
      page: 1,
      itemsPerPage: 4,
      sortBy: "topic",
      keys: ["start", "end", "target", "topic", "now"],
      itemnames: [
        "director",
        "contact",
        "start",
        "end",
        "description",
        "now",
        "target",
      ],
      items: [
        {
          contact: "1235425436",
          description: "agood vd",
          director: "sdf",
          end: "sef",
          id: "",
          now: "120",
          start: "sdf",
          target: "300",
          topic: "over",
          url: "https://z3.ax1x.com/2021/07/16/WM4PB9.jpg",
          rate: "12",
        },
      ],
      giveinfo: {
        uid: "",
        aid: "",
        money: "",
        tm: "",
      },
    };
  },
  computed: {
    numberOfPages() {
      return Math.ceil(this.items.length / this.itemsPerPage);
    },
    filteredKeys() {
      return this.itemnames.filter((key) => key !== "Topic");
    },
  },
  methods: {
    givemoney(e) {
      this.giveinfo.uid = parseInt(localStorage.getItem("id"));
      this.giveinfo.aid = e;
      this.giveinfo.money = parseFloat(this.money);
      var date = new Date();
      date = date.toISOString().slice(0, 19).replace("T", " ");
      this.giveinfo.tm = date;
      this.dialog = !this.dialog;
      this.$axios.defaults.headers.common["token"] = localStorage.getItem(
        "token"
      );
      this.$axios.post("/transaction/add", this.giveinfo).then((res) => {
        if (res.data.data) {
          alert("捐赠成功，感谢！！");
        }
      });
    },
    nextPage() {
      if (this.page + 1 <= this.numberOfPages) this.page += 1;
    },
    formerPage() {
      if (this.page - 1 >= 1) this.page -= 1;
    },
    updateItemsPerPage(number) {
      this.itemsPerPage = number;
    },
    toTrace() {
      this.$router.push("/trace");
    },
  },
  created() {
    this.$axios.defaults.headers.common["token"] = localStorage.getItem(
      "token"
    );
    const _this = this;
    this.items.length = 0;
    this.$axios.get("/activity/getAllInfo").then((res) => {
      res.data.data.forEach(function (item) {
        item.rate = item.now / item.target;
        item.rate = item.rate.toFixed(4); //数字位数过多bug
        _this.items.push(item);
      });
    });
  },
};
</script>

<style>
</style>
