<template>
    <div>
      <br><br>
    <v-row align="center" justify="center">资金流向
    </v-row>
<!--此处点击触发prepend事件调用searchdata函数  -->
    <div>
        <v-row
            align="center"
            justify="center"
        >
            <v-col cols="auto">
            <v-icon
                slot="prepend"
                @click="search"
            >mdi-magnify</v-icon>
            </v-col>
            <v-col cols="auto">
                <v-text-field
                    dense
                    label="搜索"
                    outlined
                    clearable
                    rounded
                    v-model="datasearch"
                >
                </v-text-field>
            </v-col>
        </v-row>
    </div>
    <particles-bg type="random" :canvas="{backgroundColor:'#888'}" :bg="true"/>
    <div>
        <v-switch
            v-model="reverse"
            label="Toggle reverse"
        ></v-switch>
    <v-timeline
        align-top
        :dense="$vuetify.breakpoint.smAndDown"
    >
        <v-timeline-item
            v-for="(item, i) in cards"
            :key="i"
        >
            <v-chip slot="opposite" large>{{item.tm}}</v-chip>
                <v-card
                :color="item.color"
                dark
                shaped
            >
                <v-card-title class="text-h6">
                    金额：{{item.money}}活动id:  {{item.aid}} 交易id:{{item.id}}
                </v-card-title>
                <v-card-subtitle>
                    哈希:{{item.hash}}
                </v-card-subtitle>

                <v-card-text class="white text--primary">
                        <v-img
                            :src="item.url"
                        ></v-img>
                        <p>说明:{{item.description}}</p>
                </v-card-text>
                </v-card>
        </v-timeline-item>
    </v-timeline>
    </div>
</div>
</template>

<script>
export default{
    data(){
        return {
            colors:['red lighten-2',
                    'purple darken-1',
                    'red darken-1',
                    'teal lighten-2',
                    'light-green accent-2',
                    'amber lighten-1'],
            datasearch:'',
            cards:[]
        }
    },
    methods: {  //搜索数据
        search(){
            const _this = this;
            this.cards.length = 0;
            this.$axios.defaults.headers.common['token'] = localStorage.getItem("token");
            this.$axios.get('/flow/getInfo', {params:{aid: _this.datasearch}}).then(res => {
                res.data.data.forEach(function(item){
                  console.log(item);
                    item.color = _this.colors[Math.floor((Math.random() * _this.colors.length))]
                    _this.cards.push(item);
                })
            })
        },


    },
    created(){
            const _this = this;
            this.$axios.defaults.headers.common['token'] = localStorage.getItem("token");
            this.$axios.get('/flow/getAllInfo').then(res => {
                res.data.data.forEach(function(item){
                    _this.cards.push(item);
                })});
    }
}
</script>

<style>
.logo{
    width: 105px;
    height: 104px;
    top: 28.08px;
    left: 34.88px;
}

</style>
