<template>
  <v-container >
<!--    class="pa-4 text-center"-->
    <v-dialog max-width="500" v-model="show" persistent overlay-color="#000000">

      <v-row align="center" justify="center">
        <v-text-field class="actInput" label="活动名称" :rules="rules" hide-details="auto" v-model="actInfo.topic"></v-text-field>
      </v-row>
      <v-row align="center" justify="center">
        <v-text-field class="actInput" label="目标资金" :rules="rules" hide-details="auto" v-model="actInfo.target"></v-text-field>
      </v-row>
      <v-row align="center" justify="center">
        <v-text-field class="actInput" label="联系方式" :rules="rules" hide-details="auto" v-model="actInfo.contact"></v-text-field>
      </v-row>
      <v-row align="center" justify="center">
        <v-text-field class="actInput" label="负责人" :rules="rules" hide-details="auto" v-model="actInfo.director"></v-text-field>
      </v-row>
      <v-row align="center" justify="center">
        <v-textarea class="actInput" label="活动介绍" :rules="rules" hide-details="auto" v-model="actInfo.description"></v-textarea>
      </v-row>
      <v-row align="center" justify="center">
        <v-col align="center" justify="center"><v-btn elevation="2" @click="showbgDate = true">选择开始日期</v-btn></v-col>
        <v-col align="center" justify="center"><v-btn elevation="2" @click="showedDate = true">选择结束日期</v-btn></v-col>
      </v-row>

      <v-row align="center" justify="center">
        <v-col justify="center" align="center">
          <v-btn class="buttonInput" color="success" @click="addActsuc">确定</v-btn>
        </v-col>
        <v-col justify="center">
          <v-btn class="buttonInput" color="error" @click="show = false">关闭</v-btn>
        </v-col>
      </v-row>
    </v-dialog>

    <v-dialog v-model="showbgDate" max-width="500" persistent>
        <v-date-picker v-model="date"></v-date-picker>
      <v-btn elevation="2" @click="getbgDate">确定</v-btn>
    </v-dialog>
    <v-dialog v-model="showedDate" max-width="500" persistent>
      <v-date-picker v-model="date"></v-date-picker>
      <v-btn elevation="2" @click="getedDate">确定</v-btn>
    </v-dialog>
    <v-row class="fill-height" align="center" justify="center">
      <template v-for="(item, i) in items" >
        <v-col :key="i" cols="12" md="4">
          <v-hover v-slot="{ hover }">
            <v-card :elevation="hover ? 12 : 2" :class="{ 'on-hover': hover }"  @click="update(item.method)">
              <v-img :src="item.img" height="300px">
                <v-card-title class="text-h6 white--text">

                  <v-row class="fill-height flex-column" justify="space-between">
                    <div class="actText" >
                      <p class="actstyle">{{ item.actId }}</p>
                      <p class="actstyle">{{ item.actName }}</p>
                      <p class="ratestyle">{{ item.curVale }}/{{item.preVale}}</p>
                      <v-progress-linear
                          color="deep-orange"
                          width=60%
                          height="10"
                          :value=item.actRate
                          striped
                      ></v-progress-linear>
                    </div>
                    <div class="align-self-center">
                      <v-btn
                          v-for="(icon, index) in icons"
                          :key="index"
                          :class="{ 'show-btns': hover }"
                          :color="transparent"
                          icon
                      >
                        <v-icon
                            :class="{ 'show-btns': hover }"
                            :color="transparent"
                        >
                          {{ icon }}
                        </v-icon>
                      </v-btn>
                    </div>
                  </v-row>
                </v-card-title>
              </v-img>
            </v-card>
          </v-hover>
        </v-col>
      </template>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Orgact",

  data: () => ({
    actInfo:{
      target:'',
      topic:'',
      contact:'',
      description: '',
      director: '',
      start: '',
      end: '',
    },
    show: false,
    showedDate: false,
    showbgDate: false,
    date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
    icons: ['mdi-rewind', 'mdi-play', 'mdi-fast-forward'],
    itemsql:[],
    items: [
      {
        actId: '',
        actName: '',
        curVale: '',
        preVale: '',
        actRate: '',
        img: 'https://z3.ax1x.com/2021/07/17/WQNl1f.png',
        method: "route"
      },
      {
        actId: '',
        actName: '',
        curVale: '',
        preVale: '',
        actRate: '',
        img: 'https://z3.ax1x.com/2021/07/17/WQNl1f.png',
        method: "route"
      },
      {
        actId: '',
        actName: '',
        curVale: "\\添加活动",
        preVale: '',
        actRate: '',
        img: 'https://z3.ax1x.com/2021/07/17/WQaEod.png',
        method: "inputAct"
      },
    ],
    transparent: 'rgba(255, 255, 255, 0)',
  }),
  created() {
    const _this = this;
    this.$axios.get('/activity/getAllInfo').then(res => {

      for(let i = 0;i < 2;i++){
        let len = res.data.data.length - 1;
        res.data.data[len-i].rate = res.data.data[len-i].now/res.data.data[len-i].target * 100;
        _this.items[i].actName = res.data.data[len-i].topic;
        _this.items[i].actId = res.data.data[len-i].id;
        _this.items[i].curVale = res.data.data[len-i].now;
        _this.items[i].preVale = res.data.data[len-i].target;
        _this.items[i].actRate = res.data.data[len-i].rate;
      }
    })
  },
  methods:{
    update(method){
      //触发这个参数的方法
      this[method]()
    },
    route() {
      this.$router.push("activities")
    },
    inputAct() {
      this.show = true
    },
    addActsuc() {
      this.show = false;
      this.$axios.post("/activity/add",this.actInfo).then(function(){alert("添加成功！")})
    },
    getbgDate() {
      this.actInfo.start = this.date + " 00:00:00";
      this.showbgDate = false;
    },
    getedDate() {
      this.actInfo.end = this.date + " 00:00:00";
      this.showedDate = false;
    }
  }
}
</script>

  <style scoped>
  .v-card {
    transition: opacity .4s ease-in-out;
  }

  .v-card:not(.on-hover) {
    opacity: 0.6;
  }

  .show-btns {
    color: rgba(255, 255, 255, 1) !important;
  }
  .actText{
    height: 300px;
    width: 100%;
    margin-top: 20%;
  }
  .actstyle{
    /*font-family: SourceHanSansSC;*/
    color: #000000;
    font-size: 26px;
    text-align: center;
  }
  .ratestyle{
    color: #207DE1;
    font-size: 26px;
    text-align: center;
  }
  .actInput{
    margin-left: 18px;
    margin-top: 16px;
  }
  .buttonInput{
    margin-left: 50px;
    margin-bottom: 10px;
    margin-top: 10px;
  }
</style>