<template>
<div>
    <div>
        <v-container>
            <v-row>
                <v-col
                    v-for="(item,i) in animals"
                    :key="i"
                    cols="4"
                >
                    <v-card
                        :color="colors[i%3]"
                        dark
                        max-width="400"
                    >
                        <v-card-title>
                            <v-icon
                                large
                                left
                            >
                                mdi-twitter<!---->
                            </v-icon>
                            <span class="text-h6 font-weight-light">名字：{{item.name}}</span>
                        </v-card-title>

                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>mdi-label</v-icon>
                            </v-list-item-icon>
                            <v-list-item-title>动物id：{{item.id}}</v-list-item-title>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>mdi-send</v-icon>
                            </v-list-item-icon>
                            <v-list-item-title>类型：{{item.type}}</v-list-item-title>
                        </v-list-item>
                        <v-list-item>
                            <v-list-item-icon>
                                <v-icon>mdi-thumb-up</v-icon>
                            </v-list-item-icon>
                            <v-list-item-title>活动范围：{{item.actscope}}</v-list-item-title>
                        </v-list-item>
                        <v-img :src="item.url"></v-img>
                    </v-card>
                </v-col>
            </v-row>
        </v-container>
    </div>
</div>
</template>

<script>

export default{
    name: "Animal",
    data(){
        return {
            animals: [{
                id: 0,
                actscope: "hjsdshadsah",
                name: "小明",
                type: "猫",
                url: "https://cdn.pixabay.com/photo/2020/07/12/07/47/bee-5396362_1280.jpg"
            }],
            
            colors: ["#1F7087","#952175","#26c6da"],
        }
    },
    methods: {
        toAct() {
            this.$router.push("/activities");
        },
        toTrace() {
            this.$router.push("/trace");
        }
    },
    created() {
        const _this = this;
        this.animals.length = 0;
        this.$axios.get('/animals/getAllInfo').then(res => {
           res.data.data.forEach(function(item){
                    _this.animals.push(item);
               
        })})}
}
</script>