<template>
  <div id="app">
      <v-content>
        <v-container>
          <v-card class="mx-auto my-12 outer-card"
                  max-width="374">
            <v-card-title>
              <v-card class="v-header py-2 mx-auto my-n12" max-width="364" raised>
                <v-card-title>
                  <v-row justify="center" color="white">
                    <h4>组织登录</h4>
                  </v-row>
                </v-card-title>
                <v-card-actions>
                  <v-row justify="center" color="white">

                    <v-btn icon color="white">
                      <v-icon>mdi-facebook</v-icon>
                    </v-btn>
                    <v-btn icon color="white">
                      <v-icon>mdi-twitter</v-icon>
                    </v-btn>
                    <v-btn icon color="white">
                      <v-icon>mdi-google-plus</v-icon>
                    </v-btn>
                  </v-row>
                </v-card-actions>
              </v-card>
            </v-card-title><br />
            <v-card-text class="my-4">
              <v-text-field label="Username"
                            prepend-icon="mdi-face"
                            color="purple"
                            v-model="orginfo.loginUsername"></v-text-field>
              <v-text-field label="Password"
                            prepend-icon="mdi-lock-outline"
                            color="purple"
                            v-model="orginfo.loginPassword"></v-text-field>
              </v-card-text>
              <v-card-actions>
                <v-row justify="center">
                  <v-btn normal text color="success" @click="orgLogin">               GET STARTED
                  </v-btn>
                </v-row>
              </v-card-actions>
          </v-card>
        </v-container>
      </v-content>
  </div>
</template>

<script>
export default {
  data(){
    return{
      orginfo:{
        loginUsername:'',
        loginPassword:''
      }

    }
  },
  methods:{
    orgLogin() {
      const _this = this;
      this.$axios.post('login/admin/login', this.orginfo).then(res =>{
        _this.$store.commit("SET_TOKEN", res.data.data.token)
        alert("登陆成功")
        _this.$router.push('/organization')
      })
    }
  }
}
</script>